.Users {
	position: relative;
	width: 100%;
	height: 100%;
	background: #edf0f1;
}

.header {
	display: flex;
	justify-content: space-between;

	width: 100%;
	padding: 0 2rem;

	border-bottom: 1px solid #b6bfc9;

	& p {
		margin: 0;
		padding-left: 1rem;
		text-transform: uppercase;
		line-height: 4rem;
	}

	button {
		border: 0;
		outline: none;
		background: none;
		font-size: 3rem;
		color: #498fa7;
	}
}

.table {
	thead th {
		position: relative;

		padding: 1rem;

		font-size: 1.2rem;
		color: #196480;
		border: none;
	}
	tbody {
		border-top: 1px solid #54e0e8;

		tr:last-child {
			border-bottom: 1px solid #54e0e8;
		}
	}

	input {
		margin: 0;
		height: 3rem;
		margin-left: 2rem;
	}
}
